.wrapper {
  width: 100%;
  color: var(--color-text-dark);
  font-size: 16px;

  .header {
    text-align: center;
    font-size: 2rem;
    margin: 2vh 0;
    text-wrap: wrap;
  }

  .container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: start;
    margin: 2vh 5vw;

    .card {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 90%;
      margin: 10px 10px;
      font-size: 1.5rem;
      line-height: 2rem;
      text-align: start;
    }

    .card:hover .btn {
      display: inline;
      cursor: pointer;
    }

    .btn {
      display: none;
    }
  }
}

@media (max-width: 413px) {
  .wrapper {
    width: 100%;
    color: var(--color-text-dark);
    font-size: 16px;

    .header {
      text-align: center;
      font-size: 1rem;
      margin: 2vh 0;
      text-wrap: wrap;
    }

    .container {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: start;
      margin: 2vh 5vw;

      .card {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 90%;
        margin: 10px 10px;
        font-size: 1rem;
        line-height: 2rem;
        text-align: start;
      }

      .card:hover .btn {
      }

      .btn {
      }
    }
  }
}
